<template>
  <div class="nomination-list">
    <div
      class="nomination-list--item"
      v-for="nomination in nominations"
      :key="nomination.name"
      tabindex="0"
    >
      <nomination-list-item
        @close-all="closeAll"
        :nomination="nomination"
      ></nomination-list-item>
    </div>
  </div>
</template>

<script>
import NominationListItem from './NominationListItem.vue'
export default {
  components: {
    NominationListItem,
  },
  props: {
    nominations: {
      type: Array,
      required: true,
    },
  },
  methods: {
    closeAll() {
      this.$children.forEach(item => {
        item.show = false
      })
    },
  },
}
</script>

