<template>
  <div class="nomination-item" @click="toggleShow">
    <div ref="desc" :class="`nomination-item--desc ${show ? 'show' : ''}`" @click.stop>
      <span>
        {{ nomination.description }}
      </span>
    </div>
    <div class="nomination-item--info">
      <img :src="`/landing/nominations/${nomination.svg}`" alt="Номинация" />
      <span>{{ nomination.name }}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: ['nomination'],
  data() {
    return {
      show: false,
    }
  },
  methods: {
    toggleShow() {
      this.$emit('close-all')
      if (!this.$refs.desc.classList.contains('show')) {
        this.show = !this.show
      }
    }
  }
}
</script>

