  color: #1d1d1b;

  background: #f8f8f8;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s;

  &--info {
    padding: 0 1rem;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    gap: 2rem;
    align-items: center;
  }

  &--desc {
    overflow: hidden;
    height: auto;
    position: absolute;
    z-index: 3;
    transition: max-height 0.3s cubic-bezier(0.5, 1, 0.5, 1);
    max-height: 0px;
    background: #fdfdfd;
    left: 0;
    width: 100%;
    padding: 15px;
    opacity: 0;
    line-height: 32px;
    top: 80px;
